$main-menu-bg: url("./img/bg/Main-Menu.png");
$select-char-bg: url("./img/bg/Select-Char.png");
@font-face {
  font-family: "Prompt-Reg";
  src: url("./Fonts/Prompt-Regular_0.ttf");
}
@font-face {
  font-family: "Prompt-Bold";
  src: url("./Fonts/Prompt-Bold_0.ttf");
}

body {
  margin: 0;
  #gameCanvas {
    background-image: $select-char-bg;
    background-repeat: no-repeat;
    background-size: 100% 100%;
    background-position: center;
    object-fit: cover;
    object-position: 50 50;
    position: relative;

    .start-menu {
      height: 100vh;
      width: 100%;
      position: absolute;
      background-image: $main-menu-bg;
      background-repeat: no-repeat;
      background-size: 100% 100%;
      background-position: center;
      .visa-logo {
        padding-top: 10vh;
        margin-left: auto;
        margin-right: auto;
        max-width: 65%;
        img {
          width: 100%;
        }
      }

      .game-logo {
        margin-top: 2vh;
        margin-left: auto;
        margin-right: auto;
        max-width: 90%;
        img {
          width: 100%;
        }
      }

      .play-btn {
        display: flex;
        justify-content: center;
        width: 100%;
        padding-top: 5vh;
        margin-left: auto;
        margin-right: auto;

        button {
          font-family: "Prompt-Bold";
          background-color: rgb(209, 32, 32);
          color: #ffffff;
          border-radius: 35px;
          font-size: 1.2em;
          padding: 1vh 2rem 1vh 2rem;
        }
      }

      .reward-btn {
        width: fit-content;
        padding-top: 2vh;
        margin-left: auto;
        margin-right: auto;

        button {
          font-family: "Prompt-Bold";
          background-color: rgb(209, 32, 32);
          color: #ffffff;
          border-radius: 35px;
          font-size: 1.2em;
          padding: 1vh 2.5rem 1vh 2.5rem;
        }
      }

      .lower-banner {
        padding-top: 3vh;
        margin-left: auto;
        margin-right: auto;
        max-width: 85%;
        img {
          height: 17vh;
          max-height: 17vh;
          min-width: 10rem;
          width: 100%;
          object-fit: cover;
          object-position: 0 0;
        }
      }

      .football {
        margin-left: auto;
        margin-right: auto;
        max-width: 85%;
        img {
          width: 100%;
        }
      }
    }

    .loading-menu {
      display: flex;
      align-items: center;
      justify-content: center;
      position: absolute;
      background-image: $main-menu-bg;
      background-repeat: no-repeat;
      background-size: 100% 100%;
      background-position: center;
      width: 100%;
      height: 100vh;
      margin: 0 auto;
      .fifa-logo {
        position: relative;
        max-width: 90%;
        img {
          width: 100%;
        }
      }
    }

    .select-char-menu {
      width: 100%;
      .player-head {
        padding-top: 4.5vh;
        text-align: center;
        width: 100%;
        position: absolute;
        font-family: "Prompt-Bold";
        color: white;
      }

      .button-char {
        display: flex;
        justify-content: center;
        flex-direction: column;
        position: absolute;
        top: 65%;
        width: 100%;
        gap: 10px;

        .btn-row {
          display: flex;
          justify-content: center;
          align-items: center;
          gap: 10px;

          button {
            font-family: "Prompt-Bold";
            clip-path: polygon(15% 0, 100% 0%, 85% 100%, 0% 100%);
            border-radius: 0 0.8rem 0 0.8rem;
            padding: 0.5rem;
          }

          button#load_argen {
            background-color: #6d1f49;
            color: #ffffff;
          }

          button#load_belgium {
            background-color: #c01633;
            color: #ffffff;
          }

          button#load_brazil {
            background-color: #7aa9d7;
            color: #ffffff;
          }

          button#load_england {
            background-color: #001789;
            color: #ffffff;
          }

          button#load_france {
            background-color: #f48f1c;
            color: #ffffff;
          }

          button#load_portugal {
            background-color: #991a25;
            color: #ffffff;
          }

          button#load_qatar {
            background-color: #2f9847;
            color: #ffffff;
          }

          button#load_spain {
            background-color: #016933;
            color: #ffffff;
          }
        }
      }

      .main-game-btn {
        display: flex;
        justify-content: center;
        width: 100%;
        position: absolute;
        top: 88%;

        button {
          font-family: "Prompt-Bold";
          padding: 1vh 2rem 1vh 2rem;
          background-color: red;
          color: white;
          clip-path: polygon(25% 0%, 100% 0%, 75% 100%, 0% 100%);
        }
      }
    }
  }
}
